$(window).on('load resize', function(){
    if ($(window).width() <= 992) {
        $('.blog-nav').insertBefore('.blog__content');
    } else{
        $('.blog-nav').insertBefore('.subscribe');
    }
});

$(".blog-nav__show").click(function () {
    $(".blog-nav").toggleClass("_active")
    $(".blog-nav__hidden").slideToggle()
})
